import * as React from "react";
import { withStyles, createStyles, Theme } from "@material-ui/core/styles";
import MobileStepper from "@material-ui/core/MobileStepper";
import Button from "@material-ui/core/Button";
import Paper from "@material-ui/core/Paper";
import KeyboardArrowLeft from "@material-ui/icons/KeyboardArrowLeft";
import KeyboardArrowRight from "@material-ui/icons/KeyboardArrowRight";

const style = ({ palette, spacing }: Theme) =>
	createStyles({
		root: {
			flex: 1,
			display: "flex",
		},
		container: {
			flex: 1,
			display: "flex",
			flexDirection: "column",
		},
		grid: {
			flex: 1,
			display: "inline-grid",
			gridTemplateColumns: "repeat(7, 1fr)",
		},
		header: {
			textAlign: "center",
			backgroundColor: "transparent",
		},
		cell: {
			gridColumnEnd: "span 1",
			display: "flex",
			justifyContent: "center",
			alignItems: "center",
		},
		cellLabel: {
			display: "flex",
			justifyContent: "center",
			alignItems: "center",
		},
		cellToday: {
			display: "flex",
			justifyContent: "center",
			alignItems: "center",
		},
		month: {
			fontSize: "1em",
		},
		label: {
			fontSize: "1em",
		},
		date: {
			fontSize: "1em",
		},
		moon: {
			fontSize: "1em",
		},
		mobileStepper: {
			fontSize: "1em",
			backgroundColor: "transparent",
		},
		direction: {
			fontSize: "1em",
		},
	});

const daysInMonth = (iMonth: number, iYear: number) => {
	return 32 - new Date(iYear, iMonth, 32).getDate();
};

const Moon = (year: number, month: number, day: number) => {
	const phases = ["🌑", "🌒", "🌓", "🌔", "🌕", "🌖", "🌗", "🌘"];

	let c: number = 0;
	let e: number = 0;
	let jd: number = 0;
	let b: number = 0;

	if (month < 3) {
		year--;
		month += 12;
	}

	++month;

	c = 365.25 * year;
	e = 30.6 * month;
	jd = c + e + day - 694039.09; // jd is total days elapsed
	jd /= 29.5305882; // divide by the moon cycle
	b = Math.floor(jd); // int(jd) -> b, take integer part of jd
	jd -= b; // subtract integer part to leave fractional part of original jd
	b = Math.round(jd * 8); // scale fraction from 0-8 and round

	if (b >= 8) b = 0; // 0 and 8 are the same so turn 8 into 0

	return { phase: b, emoji: phases[b] };
};

interface Day {
	label: String;
	date: number;
}

interface IProps {
	classes: any;
	theme: any;
}

const MoonComponent = (props: IProps) => {
	const today = new Date();
	const [activeStep, setActiveStep] = React.useState(today.getMonth());
	const { classes, theme } = props;

	const monthLabels = [
		"Januari",
		"Februari",
		"Mars",
		"April",
		"Maj",
		"Juni",
		"Juli",
		"Augusti",
		"September",
		"Oktober",
		"November",
		"December",
	];
	const weekDayLabels = [
		"Måndag",
		"Tisdag",
		"Onsdag",
		"Torsdag",
		"Fredag",
		"Lördag",
		"Söndag",
	];

	let year: number = today.getFullYear();
	let row: Array<Day> = [];
	let rows: Array<Array<Day>> = [];

	const months: Array<Array<Array<Day>>> = [];

	for (let month: number = 0; month <= 11; month++) {
		rows = [];

		let firstDay = new Date(year, month).getDay();
		let date: number = 1;

		for (let i: number = 0; i < 5; i++) {
			row = [];

			for (let j: number = 0; j < 7; j++) {
				if (i === 0 && j < firstDay - 1) {
					row.push({ label: "", date: -1 });
				} else if (date > daysInMonth(month, year)) {
					break;
				} else {
					row.push({ label: Moon(year, month, date).emoji, date });

					date++;
				}
			}

			rows.push(row);
		}

		months.push(rows);
	}

	const maxSteps = 12;

	const handleNext = () => {
		setActiveStep(activeStep + 1);
	};

	const handleBack = () => {
		setActiveStep(activeStep - 1);
	};

	const currentMonth = months[activeStep];
	const dayElements: Array<JSX.Element> = [];

	weekDayLabels.forEach((aWeekDay) => {
		dayElements.push(
			<div
				key={activeStep + "_" + aWeekDay}
				className={classes.cellLabel}
			>
				<span className={classes.label}>{aWeekDay}</span>
			</div>
		);
	});

	currentMonth.forEach((row, rowIndex) => {
		row.forEach((day, dateIndex) => {
			if (day.date === -1) {
				dayElements.push(
					<div
						key={activeStep + "_" + rowIndex + "_" + dateIndex}
						className={classes.cell}
					/>
				);
			} else if (
				day.date === today.getDate() &&
				activeStep === today.getMonth()
			) {
				dayElements.push(
					<div
						key={
							activeStep +
							"_" +
							rowIndex +
							"_" +
							dateIndex +
							"_" +
							day.date
						}
						className={classes.cellToday}
					>
						<span className={classes.moon}>{day.label}</span>
						<span className={classes.date}>{day.date}</span>
					</div>
				);
			} else {
				dayElements.push(
					<div
						key={
							activeStep +
							"_" +
							rowIndex +
							"_" +
							dateIndex +
							"_" +
							day.date
						}
						className={classes.cell}
					>
						<span className={classes.moon}>{day.label}</span>
						<span className={classes.date}>{day.date}</span>
					</div>
				);
			}
		});
	});

	return (
		<div className={classes.root}>
			<div className={classes.container}>
				<Paper square elevation={0} className={classes.header}>
					<span className={classes.month}>
						{monthLabels[activeStep]}
					</span>
				</Paper>
				<div className={classes.grid}>{dayElements}</div>
				<MobileStepper
					steps={maxSteps}
					position="static"
					variant="text"
					activeStep={activeStep}
					className={classes.mobileStepper}
					nextButton={
						<Button
							size="small"
							onClick={() => {
								handleNext();
							}}
							disabled={activeStep === maxSteps - 1}
						>
							<span className={classes.direction}>Next</span>
							{theme.direction === "rtl" ? (
								<KeyboardArrowLeft />
							) : (
								<KeyboardArrowRight />
							)}
						</Button>
					}
					backButton={
						<Button
							size="small"
							onClick={() => {
								handleBack();
							}}
							disabled={activeStep === 0}
						>
							{theme.direction === "rtl" ? (
								<KeyboardArrowRight />
							) : (
								<KeyboardArrowLeft />
							)}
							<span className={classes.direction}>Back</span>
						</Button>
					}
				/>
			</div>
		</div>
	);
};

export default withStyles(style, { withTheme: true })(MoonComponent);
